<template>
    <p-dialog
        header="Add"
        v-model:visible="orderBloodTestDialog"
        :style="{ width: '960px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-grid">
            <div class="p-md-7">
                <blood-test-dialog-patient-info />
                <blood-test-dialog-test-info />
                <blood-test-dialog-barcode />

                <div class="p-fluid p-formgrid p-grid p-mt-2">
                    <div class="p-field p-col">
                        <label for="bt-comments">BT Commnents</label>
                        <p-textarea id="bt-comments" rows="5" />
                    </div>
                    <div class="p-field p-col">
                        <label for="label">Label</label>
                        <p-input-text id="label" />
                        <label for="blood-machine" class="p-mt-2">
                            Blood Machine
                        </label>
                        <p-dropdown id="blood-machine" :options="[]" />
                    </div>
                </div>
            </div>
            <div class="p-md-5">
                <blood-test-dialog-test-selection />
            </div>
        </div>

        <p-button
            label="Order Test"
            class="p-button-success p-mr-2"
            @click="save"
        />

        <p-button
            label="Cancel"
            class="p-button-danger"
            @click="orderBloodTestDialog = false"
        />
    </p-dialog>
</template>

<script>
import { onMounted, provide } from "vue";
import { useBloodTestDailogInit } from "@/composable/blood-tests";

import BloodTestDialogPatientInfo from "@/components/blood-tests/patient-info.vue";
import BloodTestDialogTestInfo from "@/components/blood-tests/dialog-test-info.vue";
import BloodTestDialogBarcode from "@/components/blood-tests/dialog-barcode.vue";
import BloodTestDialogTestSelection from "@/components/blood-tests/dialog-blood-test.vue";

export default {
    components: {
        BloodTestDialogPatientInfo,
        BloodTestDialogTestInfo,
        BloodTestDialogBarcode,
        BloodTestDialogTestSelection
    },
    setup() {
        const {
            store,
            orderBloodTestDialog,
            form,
            save,
            submitted,
            isInvalidTests
        } = useBloodTestDailogInit();

        provide("form", form);
        provide("submitted", submitted);
        provide("isInvalidTests", isInvalidTests);

        onMounted(() => {
            store.dispatch("bloodTest/getBloodGroupTests");
            store.dispatch("provider/getProviders");
            store.dispatch("provider/getProviderLocations");
        });

        return { orderBloodTestDialog, form, save };
    }
};
</script>

<style scoped>
:deep() .p-fieldset .p-fieldset-legend {
    padding: 0.5rem;
    background-color: transparent;
}
</style>
